<template>
	<div v-if="genres" id="it-series">
		<vue-headful title="Series - Ink&Tales" />
		<section class="uk-section uk-section-muted uk-section-small">
			<div class="uk-container uk-container-large">
				<h1 class="uk-h2 uk-margin-remove">{{'ALL_SERIES' | translate}}</h1>
				<ul class="uk-breadcrumb uk-margin-small-top uk-flex-center">
					<li><router-link to="/" >{{'HOME' | translate}}</router-link></li>
					<li><span>{{'ALL_SERIES' | translate}}</span></li>
				</ul>
			</div>
		</section>

		<section id="it-serie-list" class="uk-section">
			<div class="uk-container uk-container-large">
				<div uk-filter="target: .js-filter">
					
					<ul id="it-serie-list-genre" class="uk-subnav uk-subnav-pill" v-if="genres && genres.length > 0">
						<li class="uk-active" uk-filter-control>
							<a href="#">Todos</a>
						</li>
						<li v-for="genre in genres" v-bind:key="genre.id" :uk-filter-control="'.tag-' + genre.id">
							<a href="#" v-if="genre.series.length">{{ genre.name }}</a>
						</li>
					</ul>

					<ul class="js-filter uk-child-width-1-2 uk-child-width-1-3@s uk-child-width-1-4@m uk-child-width-1-5@l uk-grid-small uk-grid-match uk-text-center" uk-grid>
						<li v-for="serie in series" :key="serie.id" :class="classExtraction(serie)">
							<SerieCard :serie="serie" />
						</li>
					</ul>
				</div>
			</div>
		</section>
	</div>
	<Loading v-else/>
</template>

<script>
	// import SeriesList from "../components/SeriesList.vue";
	import Loading from "../components/Loading.vue";
	import SerieCard from "../components/SerieCard.vue";
	import { SERIES_ALL_SERIES, HOME_ALL_GENRES } from "../constants/graphql";

	export default {
		mounted: function() {
			setTimeout(() => {
				this.id = this.$store.getters["auth/getId"];
				this.$apollo.queries.series.refetch();
			}, 100);
		},
		components: {
			Loading,
			SerieCard
		},
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				series: []
			};
		},
		methods: {
			classExtraction(item) {
				return item.genres.map(genre => 'tag-' + genre.id);
			}
		},
		apollo: {
			series: SERIES_ALL_SERIES,
			genres: HOME_ALL_GENRES
		}
	};
</script>